import React, { useEffect, useState } from "react";
import {
  Container,
  Typography,
  Grid,
} from "@material-ui/core";
import api from "../api/api";
import baseurl from "../api/baseurl";

const News = () => {
  const [news, setNews] = useState([]);

  useEffect(() => {
    document.title = "News";
    fetchNews();
  }, []);

  const fetchNews = async () => {
    try {
      const response = await api.get(`/api/news`);
      return setNews(response.data.data);
    } catch (err) {
      return;
    }
  };

  const renderNews = () => {
    if (news.length) {
      return (
        <div style={{ marginTop: 20 }}>
          {news.map((a, i) => {
            return (
              <Grid
                container
                style={{
                  display: "flex",
                  border: "1px solid #eee",
                  boxShadow: "1px 1px 5px #ddd",
                  marginTop: 20,
                }}
                spacing={2}
                key={i}
              >
                <Grid item xs={12} sm={12} md={3} lg={3} xl={3}>
                  <img
                    alt={a.title}
                    src={a.photo? `${baseurl}${a.photo}`:'/logo512.png'}
                    style={{
                      objectFit: "contain",
                      width: "100%",
                      border: "1px solid #eee",
                      boxShadow: "1px 1px 2px #ddd",
                    }}
                  />
                </Grid>
                <Grid item xs={12} sm={12} md={9} lg={9} xl={9}>
                  <Typography variant="h6">{a.title}</Typography>
                  <Typography paragraph style={{ whiteSpace: "pre-line" }}>
                    {a.body}
                  </Typography>
                </Grid>
              </Grid>
            );
          })}
        </div>
      );
    } else {
      return (
        <div
          style={{
            marginTop: 100,
            display: "flex",
            flexDirection: "column",
            alignItems: "center",
          }}
        >
          <Typography align="center" variant="h6">
            There is no news yet!
          </Typography>
        </div>
      );
    }
  };
  return (
    <Container maxWidth="md">
     <Typography variant="h5" gutterBottom style={{fontWeight:700, marginBottom:20,marginTop:80}} color="primary" >
            MOM NEWS
          </Typography>

      {renderNews()}

    </Container>
  );
};

export default News;
